import React, { useState, useEffect } from 'react';
import { MDBListGroup, MDBListGroupItem, MDBTypography, MDBBtn } from 'mdb-react-ui-kit';
import { Link } from 'react-router-dom';


const REGISTRATION_OPEN_DATE = new Date(2025, 1, 7, 18, 59, 59);

const Actualities = () => {
  const [timeRemaining, setTimeRemaining] = useState('');
  const [registrationStarted, setRegistrationStarted] = useState(false);

  useEffect(() => {
    const updateCountdown = () => {
      const now = new Date();
      const difference = REGISTRATION_OPEN_DATE - now;
      if (difference > 0) {
        const days = Math.floor(difference / (1000 * 60 * 60 * 24));
        const hours = Math.floor((difference / (1000 * 60 * 60)) % 24);
        const minutes = Math.floor((difference / (1000 * 60)) % 60);
        const seconds = Math.floor((difference / 1000) % 60);
        setTimeRemaining(`${days} dn ${hours} hodin ${minutes} minut ${seconds} sekund`);
      } else {
        setRegistrationStarted(true);
      }
    };

    updateCountdown();
    const interval = setInterval(updateCountdown, 1000);
    return () => clearInterval(interval);
  }, []);

  const staticActualities = [
    {
      date: '6.2.2025',
      Time: '19:00',
      header: 'Sputn registrace zvodnk na PPV2025',
      bodyText: registrationStarted
        ? `Registrace byla sputna a registrovat se mete zde:`
        : `Registrace do letonho ronku PPV 2025 bude otevena v <b>ptek 7. nora v 19.00 hodin</b>.<br/> 
          Do sputn rezervace zbv: <b>${timeRemaining}</b><br/>
          V ppad jakchkoli problm s registrac polete informaci mailem na adresu: 
          <a href="mailto:jiri.janda@elinet.cz">jiri.janda@elinet.cz</a>.`,
    },
  ];

  return (
    <div id="actualities">
      <MDBTypography tag="h4">Aktuality</MDBTypography>
      <MDBListGroup>
        {staticActualities.map(({ header, bodyText }, index) => (
          <MDBListGroupItem key={index}>
            <h6>
              <b>{header}</b>
            </h6>
            <div dangerouslySetInnerHTML={{ __html: bodyText }} />
            {registrationStarted && (
  <Link to="/registration">
    <MDBBtn color="primary" className="mt-2">
      Registrovat zvodnka
    </MDBBtn>
  </Link>
)}
          </MDBListGroupItem>
        ))}
      </MDBListGroup>
    </div>
  );
};

export default Actualities;
